import React, { useState, useEffect } from "react";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import { authFetch } from "auth";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import "assets/css/App.css";
import { Grid, Typography } from "@material-ui/core";
import Snackbar from "components/Snackbar/Snackbar.js";
import { logout } from "auth";

export default function Copeyito({ ...rest }) {
  const [openUsername, setOpenUsername] = useState(false);
  const [openEmail, setOpenEmail] = useState(false);
  const [openPassword, setOpenPassword] = useState(false);
  const [openUserList, setOpenUserList] = useState(false);
  const [user, setUser] = useState({
    name: "Default",
    email: "defaul@gmail.com",
    role: "legacy",
    password: "",
    confirmPassword: "",
  });
  const [tc, setTC] = useState(false);
  const [message, setMessage] = useState("");
  const [color, setColor] = useState("primary");
  const [userList, setUserList] = useState([]);

  useEffect(() => {
    authFetch("/api/user")
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        setUser({
          name: response.name,
          email: response.email,
          role: response.role,
          password: "",
          confirmPassword: "",
        });
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  }, [setUser]);

  useEffect(() => {
    user.role === "admin" || user.role === "boss"
      ? authFetch("/api/user/list")
          .then((response) => {
            if (response.ok) {
              return response.json();
            } else {
              throw new Error(response.statusText);
            }
          })
          .then((response) => {
            console.log(response.list);
            setUserList(response.list);
          })
          .catch((e) => handleSnackbar("danger", e.name + ": " + e.message))
      : setUserList([]);
  }, [user]);

  const handleSnackbar = (cl, ms) => {
    setColor(cl);
    setMessage(ms);
    setTC(true);
    setTimeout(function () {
      setTC(false);
      setColor("");
      setMessage("");
    }, 4000);
  };

  const handleAddClose = () => {
    setOpenUsername(false);
    var url = "/api/user/changeUsername";
    const requestOptions = {
      method: "PUT",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ name: user.name, email: user.email }),
    };
    authFetch(url, requestOptions)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        handleSnackbar("success", response.message);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  };

  const handleSuppression = (username) => {
    var url = "/api/user/list";
    const requestOptions = {
      method: "PUT",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ username: username }),
    };
    authFetch(url, requestOptions)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        handleSnackbar("success", response.message);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  };

  return (
    <div>
      <Snackbar
        place="tc"
        color={color}
        message={message}
        openUsername={tc}
        closeNotification={() => setTC(false)}
        close
      />
      <div id="login">
        <Grid container spacing={1}>
          <Grid item xs={12} align="center">
            <Typography component="h2" variant="h2">
              Profile
            </Typography>
          </Grid>
          <Grid item xs={12} align="center">
            <Typography component="h6" variant="h6">
              Hi {user.name} 🙂!
            </Typography>
          </Grid>
          {/* Username */}
          <Grid item xs={12} align="center">
            <Button
              id="ButtonDialog"
              variant="contained"
              color="secondary"
              onClick={() => setOpenUsername(true)}
            >
              Change username
            </Button>
            <Dialog
              open={openUsername}
              onClose={() => setOpenUsername(false)}
              aria-labelledby="form-dialog-title"
            >
              <DialogTitle id="form-dialog-title">
                Change your username
              </DialogTitle>
              <DialogContent>
                <Grid container spacing={1}>
                  <Grid item>
                    <TextField
                      required
                      id="new_username"
                      label="New username"
                      type="text"
                      defaultValue={user.name}
                      variant="filled"
                      onChange={(e) =>
                        setUser({ name: e.target.value, ...user })
                      }
                    />
                  </Grid>
                </Grid>
              </DialogContent>
              <DialogActions>
                <Button onClick={() => setOpenUsername(false)} color="primary">
                  Annuler
                </Button>
                <Button onClick={handleAddClose} color="primary">
                  Ajouter
                </Button>
              </DialogActions>
            </Dialog>
          </Grid>
          {/* Email */}
          <Grid item xs={12} align="center">
            <Button
              id="ButtonDialog"
              variant="contained"
              color="secondary"
              onClick={() => setOpenEmail(true)}
            >
              Change email
            </Button>
            <Dialog
              open={openEmail}
              onClose={() => setOpenEmail(false)}
              aria-labelledby="form-dialog-title"
            >
              <DialogTitle id="form-dialog-title">
                Change your email
              </DialogTitle>
              <DialogContent>
                <Grid container spacing={1}>
                  <Grid item>
                    <TextField
                      required
                      id="new_email"
                      label="New email"
                      type="text"
                      defaultValue={user.email}
                      variant="filled"
                      onChange={(e) =>
                        setUser({ email: e.target.value, ...user })
                      }
                    />
                  </Grid>
                </Grid>
              </DialogContent>
              <DialogActions>
                <Button onClick={() => setOpenEmail(false)} color="primary">
                  Annuler
                </Button>
                <Button onClick={handleAddClose} color="primary">
                  Ajouter
                </Button>
              </DialogActions>
            </Dialog>
          </Grid>
          {/* Password */}
          <Grid item xs={12} align="center">
            <Button
              id="ButtonDialog"
              variant="contained"
              color="secondary"
              onClick={() => setOpenPassword(true)}
            >
              Change password
            </Button>
            <Dialog
              open={openPassword}
              onClose={() => setOpenPassword(false)}
              aria-labelledby="form-dialog-title"
            >
              <DialogTitle id="form-dialog-title">
                Change your password
              </DialogTitle>
              <DialogContent>
                <Grid container spacing={1}>
                  <Grid item>
                    <TextField
                      required
                      id="new_password"
                      label="New password"
                      type="password"
                      variant="filled"
                      onChange={(e) =>
                        setUser({ password: e.target.value, ...user })
                      }
                    />
                  </Grid>
                  <Grid item>
                    <TextField
                      required
                      error={
                        user.password !== user.confirmPassword &&
                        user.confirmPassword !== ""
                          ? true
                          : false
                      }
                      id="new_password_confirm"
                      label="Confirm new password"
                      type="password"
                      variant="filled"
                      onChange={(e) =>
                        setUser({ confirmPassword: e.target.value, ...user })
                      }
                    />
                  </Grid>
                </Grid>
              </DialogContent>
              <DialogActions>
                <Button onClick={() => setOpenPassword(false)} color="primary">
                  Annuler
                </Button>
                <Button onClick={handleAddClose} color="primary">
                  Ajouter
                </Button>
              </DialogActions>
            </Dialog>
          </Grid>
          {/* User list */}
          {user.role === "admin" || user.role === "boss" ? (
            <Grid item xs={12} align="center">
              <Button
                id="ButtonDialog"
                variant="contained"
                color="secondary"
                onClick={() => setOpenUserList(true)}
              >
                Manage Users
              </Button>
              <Dialog
                open={openUserList}
                onClose={() => setOpenUserList(false)}
                aria-labelledby="form-dialog-title"
              >
                <DialogTitle id="form-dialog-title">
                  Manage user list
                </DialogTitle>
                <DialogContent>
                  <Grid container spacing={1}>
                    {userList.map((user) => {
                      return (
                        <Grid item xs={12} align="center">
                          <Typography component="p">
                            Email : {user[0]}, Username : {user[1]}, Role :{" "}
                            {user[2]}, Active : {user[3] === 1 ? "yes" : "no"}
                          </Typography>
                          <Button
                            onClick={() => handleSuppression(user[1])}
                            color="primary"
                          >
                            X
                          </Button>
                        </Grid>
                      );
                    })}
                  </Grid>
                </DialogContent>
                <DialogActions>
                  <Button
                    onClick={() => setOpenUserList(false)}
                    color="primary"
                  >
                    Close
                  </Button>
                </DialogActions>
              </Dialog>
            </Grid>
          ) : null}
          <Grid item xs={12} align="center">
            <Button
              id="buttonLogout"
              variant="contained"
              color="primary"
              onClick={logout}
            >
              Logout
            </Button>
          </Grid>
        </Grid>
      </div>
    </div>
  );
}
